import React from "react";
import { ReactComponent as CloseButton } from "@src/assets/images/icons/file-icons/Vector.svg";
import { Drawer, Select, Button } from "antd";
import { ReactComponent as Zeit } from "@src/assets/images/icons/file-icons/zeit.svg";
const { Option } = Select;

export default function DataFilter({ filter, setFilter }) {
  const controlDrawer = () => {
    setFilter({ ...filter, isOpened: false });
  };

  return (
    <Drawer
      className="res-filter"
      title="LỌC BẤT ĐỘNG SẢN"
      placement="right"
      onClose={controlDrawer}
      visible={filter.isOpened}
      closeIcon={<CloseButton />}
      width="462"
      footer={
        <div className="btn-group">
          <Button>XÓA BỘ LỌC</Button>
          <Button className="primary">ÁP DỤNG</Button>
        </div>
      }
    >
      <div>
        <div className="fn-title">Loại BĐS</div>
        <div className="row-custom">
          <div>Loại BĐS</div>
          <div>
            <Select
              suffixIcon={<Zeit />}
              defaultValue={filter?.typeBDS}
              placeholder="--- Loại bất động sản ---"
              onChange={(value) => setFilter({ ...filter, typeBDS: value })}
            >
              <Option key="1">Nhà đất bán</Option>
              <Option key="2">Nhà đất cho thuê</Option>
              <Option key="3">Dự án</Option>
            </Select>
          </div>
        </div>
      </div>
      <div>
        <div className="fn-title">Trạng thái</div>
        <div className="row-custom">
          <div>Trạng thái</div>
          <div>
            <Select
              suffixIcon={<Zeit />}
              defaultValue={filter?.statusBDS}
              placeholder="--- Trạng thái ---"
              onChange={(value) => setFilter({ ...filter, statusBDS: value })}
            ></Select>
          </div>
        </div>
        <div className="row-custom">
          <div>Người gửi</div>
          <div>
            <Select
              suffixIcon={<Zeit />}
              defaultValue={filter?.senderBDS}
              placeholder="--- Lựa chọn người gửi ---"
              onChange={(value) => setFilter({ ...filter, senderBDS: value })}
            ></Select>
          </div>
        </div>
      </div>
    </Drawer>
  );
}
